import React from "react";
import Navbar from "./components/Navbar";
import "./App.css";
import Home from "./components/pages/Home";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Introduction from "./components/pages/Introduction";
import Projects from "./components/pages/Projects";
import GIC from "./components/pages/GISinContext";
import Geoscripting from "./components/pages/Geoscripting";
import RGIC from "./components/pages/RGIC";
import Geotools from "./components/pages/Geotools";
function App() {
  return (
    <>
      <Router>
        <Navbar />
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/Introduction" component={Introduction} />
          <Route path="/Projects" component={Projects} />
          <Route path="/GISinContext" component={GIC} />
          <Route path="/Geoscripting" component={Geoscripting} />
          <Route path="/RGIC" component={RGIC} />
          <Route path="/Geotools" component={Geotools} />

          {/* <Route path='/sign-up' component={SignUp} /> */}
        </Switch>
      </Router>
    </>
  );
}

export default App;
